import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
const api_root = process.env.VUE_APP_ROOT_API

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pages: [],
    posts: [],
    primary_colour: '',
    secondary_colour: '',
    tertiary_colour: '',
    quarternary_colour: ''
  },
  getters: {
    sortedPosts: state=> {
      return state.posts.sort((a, b) => a.acf.order - b.acf.order)
    } 
  },
  mutations: {
    addPages (state, value) {
      state.pages = value;
    },
    addPosts (state, value) {
      state.posts = value;
    },
  },
  actions: {
    async getPages ({ commit }) {
      let data = {}
      await axios
        .get(api_root + 'getall/pages')
        .then(response => {
          data = response.data
          commit('addPages', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getPosts ({ commit }) {
      let data = {}
      await axios
        .get(api_root + 'getall/posts')
        .then(response => {
          data = response.data
          commit('addPosts', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
  }
})
